import React,{useState,useEffect} from 'react';
import './App.css';

function App() {
  // 網頁標題更改
  useEffect(() => {
    document.title = "calculate";
  }, []);
  //初始狀態
  const [Display,setDisplay]=useState("")
  const [Result,setResult]=useState("")
  //設定點擊事件
  const handlerClick = (event) =>{
    const buttonText = event.target.innerText;
        // 更新狀態 
        if (buttonText==="AC"){
          //將數字跟結果都清空
          setDisplay("");
          setResult("");
        }else if (buttonText==="="){
          try{
            setDisplay("");
            setResult(eval(Display)|| '');
          } catch(error){
            setResult('error');
          }
        }else{
          setDisplay(prevText => prevText + buttonText);
        }
      };
  return (
    <div className="App">
      <div className="calculate">
        {/* 展示出點擊效果 */}
      <p>{Display}{Result}</p>
      </div>
      <div>
            <button className="button_ac" onClick={handlerClick}>AC</button>
            <button className="button_ac" onClick={handlerClick}>+/-</button>
            <button className="button_ac" onClick={handlerClick}>%</button>
            <button className="button_add" onClick={handlerClick}>/</button>
        </div>
        <div>
            <button className="button" onClick={handlerClick}>7</button>   
            <button className="button" onClick={handlerClick}>8</button>   
            <button className="button" onClick={handlerClick}>9</button>
            <button className="button_add" onClick={handlerClick}>x</button>   
        </div> 
        <div>
            <button className="button" onClick={handlerClick}>4</button>
            <button className="button" onClick={handlerClick}>5</button>
            <button className="button" onClick={handlerClick}>6</button>
            <button className="button_add" onClick={handlerClick}>-</button>   
        </div>
        <div>       
            <button className="button" onClick={handlerClick}>1</button>
            <button className="button" onClick={handlerClick}>2</button>
            <button className="button" onClick={handlerClick}>3</button>
            <button className="button_add" onClick={handlerClick}>+</button>  
        </div>
        <div>
            <button className="button0" onClick={handlerClick}>0</button>
            <button className="button" onClick={handlerClick}>.</button>
            <button className="button_add" onClick={handlerClick}>=</button>   
        </div>     
      </div>

  );
}

export default App;
